@use 'base/variables' as v;

.citas{
    list-style: none;
    padding: 0;

    li{
        padding: 1rem;
        border-top: 1px solid v.$negro;
        p{
            font-weight: v.$bold;
            color: v.$negro;
        }   

        span,
        .servicio{
            font-weight: v.$regular;
            color: v.$negro;
        }

    }
}

.servicios{
    @extend .citas;

    .acciones{
        margin: 0;

    }
    .boton,
    .boton-eliminar{
        margin: 0;
    }
}