@use 'base/variables' as v;
@use 'base/mixins' as m;

.listado-servicios{
    display: grid;
    gap: 2rem;
    @include m.tablet{
        @include m.grid(4, 4rem);
    }
    .servicio{
        background-color: v.$gris;
        padding: 3rem;
        border-radius: 1rem;
        transition-property: transform;
        transition-duration: 300ms;
        text-align: center;
        @include m.tablet{
            text-align: left;
        }


        &.seleccionado{
            background-color: v.$negro;
            p{
                color: v.$blanco;
            }
        }   

        &:hover{
            transform: scale(1.1);
        }

        p{
            color: v.$negro;
            margin: 0 0 calc(v.$separacion/4) 0;
        }
        .precio-servicio{
            color: v.$azul;
            font-weight: v.$bold;
            font-size: 3rem;
            margin: 0;
        }
    }
}